.chat-send-wrapper {
  position: relative;
  z-index: 2;
  padding: 12px 36px;
  border-top: 1px solid #e5e9f2;
  border-bottom: 2px solid #e5e9f2;
}
.chat-send-wrapper .pop-none {
  display: none;
}
.chat-send-wrapper .emoji-pop {
  width: 355px;
  height: 370px;
  z-index: 1;
  position: absolute;
  top: -358px;
}
.chat-send-wrapper .emoji-pop .emoji-mart-preview {
  display: none;
}
.chat-send-wrapper .tool-bar {
  display: flex;
  padding: 10px;
}
.chat-send-wrapper .tool-bar .btn-icon {
  width: 48px;
  height: 48px;
  padding: 0 5px;
  border: none;
  border-radius: 50%;
  font-size: 22px;
  cursor: pointer;
}
.chat-send-wrapper .tool-bar .btn-icon:hover {
  color: #90EE90;
}
.chat-send-wrapper .tool-bar .btn-icon:focus {
  color: #90EE90;
}
.chat-send-wrapper .form-row {
  display: flex;
  align-items: center;
}
.chat-send-wrapper .form-row .ant-form-item {
  margin: 0;
}
.chat-send-wrapper .form-row .input-send {
  flex-basis: 0;
  flex-grow: 1;
  padding: 0 5px;
}
.chat-send-wrapper .form-row .input-send .send-area {
  height: 150px;
  padding: 6px 12px;
  font-size: 16px;
  border: none;
  resize: none;
}
.chat-send-wrapper .form-row .input-send .send-area:focus {
  border: none;
  box-shadow: none;
}
.chat-send-wrapper .form-row .btn-send {
  width: 48px;
  height: 48px;
  padding: 0 5px;
  border: none;
  border-radius: 50%;
  background-color: #90EE90;
  font-size: 22px;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.chat-send-wrapper .form-row .btn-send:hover {
  background-color: #90EE90;
}
.chat-send-wrapper .bf-controlbar {
  display: none;
}
.chat-send-wrapper .bf-content {
  height: 250px;
}
