.chat-tool-btn {
  width: 48px;
  height: 48px;
  padding: 0 5px;
  border: none;
  border-radius: 50%;
  font-size: 22px;
  cursor: pointer;
}
.chat-tool-btn:hover {
  color: #90EE90;
}
.chat-tool-btn:focus {
  color: #90EE90;
}
.facebag-img {
  max-width: 80px;
  margin: 3px;
}
.facebag-img :hover {
  filter: alpha(opacity=80);
  -moz-opacity: 0.8;
  opacity: 0.8;
}
